












import Vue from 'vue'

export default Vue.extend({
  computed: {
    nameQuery: {
      get() {
        return this.$store.state.nameQuery
      },
      set(value) {
        this.$store.commit('search', {
          nameQuery: value,
          paramQuery: this.paramQuery
        })
      }
    },
    paramQuery: {
      get() {
        return this.$store.state.query
      },
      set(value) {
        this.$store.commit('search', {
          nameQuery: this.nameQuery,
          paramQuery: value
        })
      }
    }
  }
})
